
#camsource {
  background: #feefd1;
  border: 2px solid #feefd1;
  border-radius: 15px;
  padding: 10px;
  width: 80vw;
  height: 60vw;
  max-width: 320px;
  max-height: 240px;
}
#uploadField {
  max-width: 300px;
}
@media screen and (max-width: 454px) {
#uploadField {
    max-width: 150px;
}
}
#uploadButton {
  cursor: pointer;
  z-index: 1;
  display: block;
  margin: auto;
  min-height: 300px;
  background: url("../assets/uploadfile.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
@media screen and (max-width: 454px) {
#uploadButton {
    min-height: 150px;
}
}
#upload {
  display: none;
}
[role="subTitle"] {
  margin-bottom: 3rem;
}



